  animation: spinner 0.6s linear infinite;
}
.table-spinner:after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  height: 100%;
  width: 100%;
  display: block;
  top: 0;
  right: 0;
  background: #fff;
  border-radius: 16px;
}

