/*                                   Side bar styles                                */
/* ================================================================================ */
.custom-sidebar-container {
  position: relative;
}
.custom-sidebar-container .custom-sidebar-box-container {
  position: fixed;
  z-index: 10;
  top: 0;
  right: -50%;
  bottom: 0;
  width: 50%;
  height: 100vh;
  padding: 0;
  -webkit-transition: right 0.4s cubic-bezier(0.05, 0.74, 0.2, 0.99);
  -moz-transition: right 0.4s cubic-bezier(0.05, 0.74, 0.2, 0.99);
  -o-transition: right 0.4s cubic-bezier(0.05, 0.74, 0.2, 0.99);
  transition: right 0.4s cubic-bezier(0.05, 0.74, 0.2, 0.99);
  border-left: 1px solid rgba(0, 0, 0, 0.05);
  background-color: #fff;
  -webkit-box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
}
.custom-sidebar-container .custom-sidebar-box-container .custom-sidebar-close-icon {
  display: none;
}
.custom-sidebar-container .custom-sidebar-box-container.open {
  right: 0;
}
.custom-sidebar-container .custom-sidebar-box-container.open .custom-sidebar-box-shadow {
  z-index: 10;
  width: 100vw;
  display: block;
  content: "";
  height: 100%;
  position: absolute;
  background: #061c33;
  transition: all 0.3s ease-in;
  opacity: 0.4;
  right: 100%;
  top: 0;
  cursor: auto;
}
.custom-sidebar-container .custom-sidebar-box-container.open .custom-sidebar-close-icon {
  margin-left: auto;
  color: #f44336;
  position: absolute;
  top: calc(50% - 30px);
  left: -60px;
  height: 60px;
  width: 60px;
  background: #fff;
  z-index: 12;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
}
.custom-sidebar-container .custom-sidebar-box-container .custom-sidebar-box-content {
  padding: 16px;
  overflow: hidden;
  overflow-y: auto;
  height: calc(100vh - 48px);
  z-index: 10;
}
.custom-sidebar-container .custom-sidebar-box-container .custom-sidebar-box-content.no-footer {
  height: 100vh;
}
.custom-sidebar-container .custom-sidebar-box-container .custom-sidebar-box-footer {
  position: absolute;
  height: 48px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 0 16px;
  width: 100%;
  background: #fff;
  border-top: 1px solid #ccc;
  left: 0;
  bottom: 0;
  z-index: 11;
}